import { css, } from 'lit-element';

export default css`
:host {
  display: block;
  font-family: sans-serif;
}

*, *::before, *::after {
  font: inherit;
  box-sizing: inherit;
}

.bp {
  height: 26px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.bar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #D0D1D4;
  color: #fff;
  border: 4px solid var(--bbva-core-demo-resizable-viewport-background-color, #f0f0f0);
  border-top: 0;
  box-sizing: content-box;
  font-size: 0.625rem;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  overflow: auto;
  user-select: none;
}

.dragger {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  right: 2px;
  bottom: 2px;
}

.dragger-path {
  fill: var(--bbva-core-demo-resizable-viewport-dragger-inactive-color, #fff);
}
.bar.selected {
  background-color: #343B4A;
  color: #fff;
}

.bar.selected ~ .bar {
  background-color: transparent;
}

.bar.selected ~ .bar .dragger-path {
  fill: var(--bbva-core-demo-resizable-viewport-dragger-color, #A9ABAD);
}

.bar.selected ~ .bar:hover {
  background-color: #252a35;
}

.bar.selected .dragger-path {
  fill: var(--bbva-core-demo-resizable-viewport-dragger-color, #A9ABAD);
}

.bar:hover {
  background-color: #252a35;
}

.bar:hover ~ .bar {
  background-color: transparent;
}

.bar:hover ~ .bar .dragger-path {
  fill: var(--bbva-core-demo-resizable-viewport-dragger-color, #A9ABAD);
}

.bar:hover .label {
  visibility: visible;
  position: relative;
  background-color: #252a35;
}

.bar:hover ~ .bar .label {
  visibility: hidden;
}

.bar:hover .dragger-path {
  fill: var(--bbva-core-demo-resizable-viewport-dragger-color, #A9ABAD);
}

.label {
  text-transform: uppercase;
  visibility: hidden;
  background-color: #252a35;
}

.selected .label {
  visibility: visible;
  background-color: #343B4A;
}

.btn-reset {
  font-family: inherit;
  font-size: 0.75rem;
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  color: #fff;
  border-radius: 3px;
  padding: 4px 8px;
  cursor: pointer;
  outline: none;
  position: relative;
  z-index: 9;
  user-select: none;
  margin-right: 10px;
  margin-top: -2px;
}

.btn-reset:hover,
.btn-reset:active,
.btn-reset:focus {
  background-color: rgba(0, 0, 0, 0.8);
}
`;
