import { ambients as backgroundColors } from '@bbva-web-components/bbva-foundations-styles';

export function _getWindowDemoHelperAmbients() {
  const demoHelpers = window.DemoHelpers || {};
  const result = {};
  const ambients = Object.keys(demoHelpers).filter(
    item => item.toLowerCase().indexOf('ambient') >= 0,
  );
  ambients.forEach(ambient => {
    Object.keys(demoHelpers[ambient]).forEach(item => {
      result[item] = result[item] || '';
      result[item] += demoHelpers[ambient][item].cssText;
    });
  });
  return result;
}

export function _ambientBackgrounds() {
  return Object.keys(backgroundColors).map(
    item => `[ambient=${item}] { background-color: ${backgroundColors[item]};}`,
  );
}
