import { css } from 'lit-element';

export default css`
  :host {
    display: block;
    position: relative;
    box-sizing: border-box;
  }

  :host([hidden]),
  [hidden] {
    display: none !important;
  }

  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }

  .slot-select ::slotted(select) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    opacity: 0;
    z-index: -1;
    pointer-events: none;
  }

  .label:not(:empty) {
    display: block;
    margin-bottom: 0.25rem;
  }

  .button {
    display: block;
    border-radius: 0;
    font: inherit;
    font-size: 1rem;
    line-height: 1.5rem;
    text-align: left;
    padding: 0.5em 2rem 0.5rem 1rem;
    height: 3rem;
    width: 100%;
    background: transparent;
    position: relative;
    cursor: pointer;
    border: 1px solid #666;
    border-radius: 2px;
  }

  .button::-moz-focus-inner {
    border: 0;
  }

  .button::after {
    content: '';
    width: 0;
    height: 0;
    border-left: 0.5rem solid transparent;
    border-right: 0.5rem solid transparent;
    border-top: 0.5rem solid #aaa;
    position: absolute;
    right: 0.5rem;
    top: 50%;
    transform: translateY(-50%);
  }

  .button[aria-expanded='true']::after {
    border-top: 0;
    border-bottom: 0.5rem solid #aaa;
  }

  :host(:not([focus-visible])) .button {
    outline: none;
  }

  .button[disabled] {
    opacity: 0.5;
    cursor: default;
  }

  :host([readonly]) .button {
    cursor: default;
    background-color: #e9e9e9;
  }

  .button[aria-invalid='true'] {
    background-color: #ffdddd;
  }

  .options-layer {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    width: 100%;
    padding: 1rem 0;
    overflow-y: auto;
    border: 1px solid #aaa;
    border-top: 0;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
  }

  .options-layer.opened {
    opacity: 1;
    visibility: visible;
  }
`;
