import { css, } from 'lit-element';

export default css`
:host {
  display: block;
  box-sizing: border-box;
  letter-spacing: 1px;
  --_value-color: #565C67;
  --_underline-color: #C6C8CF;
  --_icon-color: #565C67;
  --_label-color: #ABACAF;
}

:host([hidden]), [hidden] {
  display: none!important;
}

*, *::before, *::after {
  box-sizing: inherit;
}

.label {
  font-size: 0.75rem;
  margin-bottom: 0.625rem;
  color: #ABACAF;
}

.button {
  background-color: transparent;
  line-height: 1rem;
  height: auto;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: var(--_value-color);
  font-size: 0.875rem;
  padding: 0;
  border: none;
  outline: none;
  overflow: hidden;
}

.button::after {
  display: none;
}

span {
  display: block;
}

.text {
  padding: 0px 1.25rem 0.5rem 0px;
  height: 1.5rem;
}

.line {
  height: 8px;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}

.underline {
  width: 100%;
  height: 2px;
  position: relative;
  transform-origin: left center;
  overflow: hidden;
  transition: transform .3s;
}

.underline::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--_underline-color);
  transform: translateX(-1rem);
  transition: transform .3s, background-color .3s;
}

.icon {
  position: absolute;
  bottom: auto;
  right: -4px;
  top: auto;
  width: 1rem;
  height: 1rem;
  transform-origin: center top;
  fill: var(--_icon-color);
  transition: transform .3s;
}

[aria-expanded=true] .icon {
  transform: rotate(180deg);
}

.button:hover,
:host([focus-visible]) .button:focus,
[aria-expanded=true] {
  --_value-color: #224FBD;
  --_underline-color: #343B4A;
  --_icon-color: #343B4A;
}

.button:hover .underline::after,
:host([focus-visible]) .button:focus .underline::after {
  transform: translateX(-12px);
}

.button[aria-expanded=true] .underline::after {
  transform: translateX(0);
}

.options-layer {
  padding: 0;
  border: none;
  position: absolute;
  width: 100vw;
  z-index: 2;
  margin: 0 0 .5rem;
  color: #D0D1D4;
  transform: scale(0, 0);
  transition: transform .3s;
  display: none;
  background: transparent;
}
:host([animating]) .options-layer {
  display: flex;
}
.options-layer.opened {
  z-index: 3;
  display: flex;
  transform: scale(1, 1);
}

.layer-wrapper {
  max-width: none;
  min-width: 0;
  max-height: none;
  min-height: 0;
  position: relative;
  display: flex;
  overflow: auto;
}
.layer-content {
  background-color: #343B49;
  max-height: 100%;
  padding: 1.5rem;
  flex: auto;
  overflow: auto;
}

.options-layer.top {
  top: 100%;
}
.options-layer.bottom {
  bottom: 100%;
}
.options-layer.left {
  left: 0;
}
.options-layer.left.top {
  transform-origin: top left;
}
.options-layer.left.bottom {
  transform-origin: bottom left;
}
.options-layer.right {
  justify-content: flex-end;
  right: 0;
  left: auto;
}
.options-layer.right.top {
  transform-origin: top right;
}
.options-layer.right.bottom {
  transform-origin: bottom right;
}
`;
