/*
`<bbva-core-demo-case>` is aimed to be used inside a `bbva-core-demo-helper` component. It expects to have a `template` tag as content. Any content inside that template tag is exposed as a stringfied HTML in the `inner` property. It can also receive `heading` and `description` attributes, which will set a title and description for the demo case when used in the demo helper.

Example:

```html
<bbva-core-demo-helper>
  <bbva-core-demo-case heading="Demo title" description="Demo description">
    <template>
      Content here
    </template>
  </bbva-core-demo-case>
</bbva-core-demo-helper>
```

* @customElement bbva-core-demo-case
* @HTMLElement
*/
export class BbvaCoreDemoCase extends HTMLElement {
  static get observedAttributes() {
    return ['heading', 'description'];
  }

  static get is() {
    return 'bbva-core-demo-case';
  }

  attributeChangedCallback(name, oldValue, newValue) {
    this[name] = newValue;
  }

  get inner() {
    return this.querySelectorAll('template')[0].innerHTML;
  }

  getInner() {
    return this.inner;
  }
}
