import { LitElement, html, svg } from 'lit-element';
import { getComponentSharedStyles } from '@bbva-web-components/bbva-core-lit-helpers';
import * as Gestures from '@bbva-web-components/bbva-core-lit-helpers/utils/gestures.js';
import styles from './BbvaCoreDemoResizableViewport-styles.js';

const dragger = svg`
<svg width="10" height="10" viewBox="0 0 10 10" class="dragger">
  <path class="dragger-path" d="M8.293.293c.39-.39 1.024-.39 1.414 0 .39.39.39 1.024 0 1.414l-8 8c-.39.39-1.024.39-1.414 0-.39-.39-.39-1.024 0-1.414l8-8zm0 6c.39-.39 1.024-.39 1.414 0 .39.39.39 1.024 0 1.414l-2 2c-.39.39-1.024.39-1.414 0-.39-.39-.39-1.024 0-1.414l2-2z" fill-rule="nonzero"></path>
</svg>
`;

const DEFAULT_BREAKPOINTS = {
  mobile: { width: 320, height: 640, name: 'Mobile' },
  tablet: { width: 768, height: 1024, name: 'Tablet' },
  desktop: { width: 1024, height: 800, name: 'Desktop' },
};

function _sortBreakpoints(breakpoints) {
  const arr = [];

  Object.keys(breakpoints).forEach((key, i) => {
    arr[i] = JSON.parse(JSON.stringify(breakpoints[key]));
    arr[i].id = key;
    arr[i].name = arr[i].name || key;
  });

  return arr.sort((a, b) => b.width - a.width);
}

/**
`bbva-core-demo-resizable-viewport` provides resizable (through dragging) bars matching specified resolutions, and fires events with selected bar, width and height with each change. Event info can be used to easily resize any other element.

```html
<bbva-core-demo-resizable-viewport
breakpoints='{
  "mobile": { "width": 320, "height": 640, "name": "Mobile" },
  "tablet": { "width": 768, "height": 1024, "name": "Tablet" }
}'
selected-key="tablet"
>
</bbva-core-demo-resizable-viewport>
```

* @customElement bbva-core-demo-resizable-viewport
* @polymer
* @LitElement
 */
export class BbvaCoreDemoResizableViewport extends LitElement {
  static get is() {
    return 'bbva-core-demo-resizable-viewport';
  }

  static get properties() {
    return {
      /**
       * List of available breakpoints.
       */
      breakpoints: {
        type: Object,
      },

      /**
       * Selected breakpoint key.
       */
      selectedKey: {
        type: String,
        attribute: 'selected-key',
      },

      _sortedBreakpoints: {
        type: Array,
        attribute: false,
      },

      _selectedBreakpoint: {
        type: Object,
      },

      _breakpointsChanged: {
        type: Boolean,
        attribute: false,
      },
    };
  }

  constructor() {
    super();
    this.selectedKey = 'mobile';
    this.breakpoints = DEFAULT_BREAKPOINTS;
    this._breakpointsChanged = false;
  }

  get breakpoints() {
    return this._breakpoints;
  }

  set breakpoints(newValue) {
    const oldValue = this._breakpoints;
    if (newValue !== oldValue) {
      this._breakpoints = newValue || DEFAULT_BREAKPOINTS;
      this._sortedBreakpoints = _sortBreakpoints(this._breakpoints);
      this.requestUpdate('breakpoints', oldValue);
    }
  }

  get selectedKey() {
    return this._selectedKey;
  }

  set selectedKey(newValue) {
    const oldValue = this._selectedKey;
    if (newValue !== oldValue) {
      this._selectedKey = newValue;
      this.requestUpdate('selectedKey', oldValue);
    }
  }

  updated(changedProps) {
    if (changedProps.has('breakpoints') || changedProps.has('selectedKey')) {
      this._selectedBreakpoint = this._breakpoint;
    }
    if (changedProps.has('breakpoints')) {
      this._addListeners();
    }
    if (changedProps.has('_selectedBreakpoint')) {
      /**
       * Fired when selected breakpoint changes
       * @event breakpoint-change
       */
      this.dispatchEvent(
        new CustomEvent('breakpoint-change', {
          detail: {
            width: this._selectedBreakpoint.width,
            height: this._selectedBreakpoint.height,
            name: this._selectedBreakpoint.name,
          },
        }),
      );
    }
    if (changedProps.has('selectedKey')) {
      /**
       * Fired when selected breakpoint key changes
       * @event key-change
       */
      this.dispatchEvent(
        new CustomEvent('key-change', {
          detail: {
            value: this.selectedKey,
          },
        }),
      );
    }
  }

  _addListeners() {
    const elems = this.shadowRoot.querySelectorAll('.bar');
    this._sortedBreakpoints.forEach((item, index) => {
      Gestures.addListener(elems[index], 'track', e => {
        this._onTrack(e, index);
      });
    });
  }

  static get styles() {
    return [styles, getComponentSharedStyles('bbva-core-demo-resizable-viewport-shared-styles')];
  }

  render() {
    const items = this._sortedBreakpoints.map(
      (item, index) => html`
        <div
          class="bar ${this.selectedKey === item.id ? 'selected' : ''}"
          style="width: ${item.width}px; z-index: ${index};"
          @click="${() => this._setSelectedKey(item)}"
        >
          <div class="label">${item.name}  ${item.width}px</div>
          ${dragger}
        </div>
      `,
    );
    return html`
      <div class="bp">
        ${items}
        ${this._breakpointsChanged
          ? html` <button @click="${this.reset}" class="btn-reset">reset</button> `
          : ''}
      </div>
    `;
  }

  get _breakpoint() {
    if (this._sortedBreakpoints && this.selectedKey) {
      const result = this._sortedBreakpoints.find(bp => bp.id === this.selectedKey, this);
      return result || this._sortedBreakpoints[this._sortedBreakpoints.length - 1];
    }
    return null;
  }

  _setSelectedKey(item) {
    if (!this._dragging) {
      this.selectedKey = item.id;
    }
  }

  /**
   * Restore the original breakpoints width.
   */
  reset() {
    this._sortedBreakpoints = _sortBreakpoints(this.breakpoints);
    this._selectedBreakpoint = this._breakpoint;
    this._breakpointsChanged = false;
  }

  _onTrack(e, index) {
    const state = {
      start: this._onTrackStart.bind(this),
      track: this._onTrackX.bind(this),
      end: this._onTrackEnd.bind(this),
    };

    state[e.detail.state](e, index);
  }

  _onTrackStart(e) {
    this._dragging = Math.abs(e.detail.dy) < Math.abs(e.detail.dx);
  }

  _onTrackX(e, index) {
    if (!this._dragging) {
      return;
    }
    this._width = this._width || parseInt(window.getComputedStyle(e.currentTarget).width, 10);
    this._sortedBreakpoints[index].width = this._width + e.detail.dx * 2;
    this.requestUpdate('_sortedBreakpoints');
  }

  _onTrackEnd(e, index) {
    if (!this._dragging) {
      return;
    }
    const item = this._sortedBreakpoints[index];

    // show reset button if any of the breakpoints has changed its original width
    if (item.width !== this.breakpoints[item.id].width) {
      this._breakpointsChanged = true;
    }

    this._updateSelected(index);
    this._width = null;

    setTimeout(() => {
      this._dragging = false;
    }, 0);
  }

  _updateSelected(index) {
    const selectedKeyIndex = this._sortedBreakpoints.findIndex(bp => bp.id === this.selectedKey);
    if (index === selectedKeyIndex) {
      this._selectedBreakpoint = this._breakpoint;
      this.requestUpdate('_selectedBreakpoint');
    }
  }
}
