import './boot.js';

// unique global id for deduping mixins.
let dedupeId = 0;

/* eslint-disable valid-jsdoc */
/**
 * Wraps an ES6 class expression mixin such that the mixin is only applied
 * if it has not already been applied its base argument. Also memoizes mixin
 * applications.
 *
 * @template T
 * @param {T} mixin ES6 class expression mixin to wrap
 * @return {T}
 * @suppress {invalidCasts}
 */
export const dedupingMixin = function (mixinParam) {
  const mixin = mixinParam;
  let mixinApplications = mixin.__mixinApplications;
  if (!mixinApplications) {
    mixinApplications = new WeakMap();
    mixin.__mixinApplications = mixinApplications;
  }
  // maintain a unique id for each mixin
  const mixinDedupeId = dedupeId;
  dedupeId += 1;
  function dedupeMixin(base) {
    const baseSet = base.__mixinSet;
    if (baseSet && baseSet[mixinDedupeId]) {
      return base;
    }
    const map = mixinApplications;
    let extended = map.get(base);
    if (!extended) {
      extended = mixin(base);
      map.set(base, extended);
    }
    // copy inherited mixin set from the extended class, or the base class
    // NOTE: we avoid use of Set here because some browser (IE11)
    // cannot extend a base Set via the constructor.
    const mixinSet = Object.create(extended.__mixinSet || baseSet || null);
    mixinSet[mixinDedupeId] = true;
    extended.__mixinSet = mixinSet;
    return extended;
  }

  return dedupeMixin;
};
/* eslint-enable valid-jsdoc */
