import { html, svg } from 'lit-element';
import { BbvaCoreOption } from '@bbva-web-components/bbva-core-select';
import styles from './BbvaCoreDemoDropdownOption-styles.js';

const check = svg`
<svg viewBox="0 0 260 260" focusable="false" style="width: 100%; height: 100%; overflow: hidden; display: block;" preserveAspectRatio="xMidYMid meet">
  <path fill-rule="evenodd" d="M42 140.78l14.14-14.14 49.5 49.5L218.78 63l14.14 14.14-112.38 112.38-14.9 14.9L42 140.78z"/>
</svg>
`;

/*
`bbva-core-demo-dropdown-option` extends `bbva-core-option` to provide option tags for `bbva-core-demo-dropdown-menu` with custom styling. Otherwise, it works exactly as `bbva-core-option` does.

* @customElement bbva-core-demo-dropdown-option
* @polymer
* @LitElement
*/
export class BbvaCoreDemoDropdownOption extends BbvaCoreOption {
  static get is() {
    return 'bbva-core-demo-dropdown-option';
  }

  static get styles() {
    return [super.styles, styles];
  }

  render() {
    return html`
      <span class="content">
        <span class="text">
          <slot></slot>
        </span>
        <span class="check">
          ${this.selected ? html` ${check} ` : ''}
        </span>
      </span>
      <span class="line"></span>
    `;
  }
}
