import { css } from 'lit-element';

export default css`
  :host {
    display: block;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    padding: 0 2em 0 1em;
    font-size: 1rem;
    line-height: 1.5rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    outline: none;
    cursor: pointer;
  }

  :host([hidden]),
  [hidden] {
    display: none !important;
  }

  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }

  :host([tabindex='0']) {
    background: #e9e9e9;
  }

  :host([aria-selected='true'])::after {
    content: '';
    position: absolute;
    right: 0.5em;
  }

  :host([aria-disabled='true']) {
    opacity: 0.5;
    cursor: default;
  }
`;
