import { LitElement, html } from 'lit-element';
import { getComponentSharedStyles } from '@bbva-web-components/bbva-core-lit-helpers';
import stylesOption from './BbvaCoreOptionStyles.js';
/**
![LitElement component](https://img.shields.io/badge/litElement-component-blue.svg)

BbvaCoreOption is a base class for custom option elements aimed to be used inside elements extending 'BbvaCoreSelect'. It provides similar functionality to native 'option' tags, but it can provide its own styling and properties. So, it's specially aimed to be extended for encapsulating styles.

@customElement bbva-core-option
@polymer
@LitElement
 */
export class BbvaCoreOption extends LitElement {
  static get is() {
    return 'bbva-core-option';
  }

  static get properties() {
    return {
      /**
       * Name for option
       */
      name: {
        type: String,
      },

      /**
       * If true, option is disabled
       */
      disabled: {
        type: Boolean,
      },

      /**
       * If true, option is selected
       */
      selected: {
        type: Boolean,
      },

      /**
       * Value for option
       */
      value: {
        type: String,
      },

      /**
       * If true, option is focused
       */
      focused: {
        type: Boolean,
      },
    };
  }

  get text() {
    return this.textContent;
  }

  constructor() {
    super();
    this.disabled = false;
    this.selected = false;
    this.focused = false;

    this.addEventListener('mouseenter', this._onMouseEnter.bind(this));
    this.addEventListener('mouseleave', this._onMouseLeave.bind(this));
  }

  firstUpdated(changedProps) {
    super.firstUpdated && super.firstUpdated(changedProps);
    this.setAttribute('role', 'option');
  }

  updated(changedProps) {
    super.updated && super.updated(changedProps);

    if (changedProps.has('disabled')) {
      this.setAttribute('aria-disabled', this.disabled.toString());
    }

    if (changedProps.has('selected')) {
      this.setAttribute('aria-selected', this.selected.toString());
    }

    if (changedProps.has('selected') || changedProps.has('value')) {
      /**
       * Fired when selected or value changes
       * @event option-change
       */
      this.dispatchEvent(
        new CustomEvent('option-change', {
          bubbles: true,
          detail: {
            selected: this.selected,
            disabled: this.disabled,
            value: this.value,
          },
        }),
      );
    }

    if (changedProps.has('focused')) {
      this.setAttribute('tabindex', this.focused ? '0' : '-1');
      if (this.focused) {
        this.focus();
        /**
         * Fired when focused is set to true
         * @event option-focus
         */
        this.dispatchEvent(
          new CustomEvent('option-focus', {
            bubbles: true,
          }),
        );
      }
    }
  }

  static get styles() {
    return [stylesOption, getComponentSharedStyles('bbva-core-option-shared-styles')];
  }

  render() {
    return html` <span><slot></slot></span> `;
  }

  _onMouseEnter() {
    if (!this.disabled) {
      this.focused = true;
    }
  }

  _onMouseLeave() {
    this.focused = false;
  }
}
