import { css, } from 'lit-element';

export default css`
:host {
  display: block;
  box-sizing: border-box;
}

:host([hidden]), [hidden] {
  display: none!important;
}

*, *::before, *::after {
  box-sizing: inherit;
}

.toast {
  display: flex;
  /* display toasts in reverse order without reversing the array */
  flex-direction: column-reverse;
  align-items: flex-start;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 103;
  min-height: 3rem;
  min-width: 18rem;
  border-radius: 2px;
  margin: .75rem;
  font-size: .875rem;
  line-height: 1.25rem;
  cursor: default;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
  transform: translateY(100px);
}

:host([opened]) .toast {
  opacity: 1;
  transform: translateY(0);
}

:host([align-top]) .toast {
  bottom: auto;
  top: var(--bbva-core-demo-event-toaster-align-top-margin, 0);
}

.highlight {
  font-family: Consolas, Menlo, Monaco, monospace;
  color: var(--bbva-core-demo-event-toaster-highlight-color, #F8CD51);
  letter-spacing: normal;
}

p {
  margin: 0;
}

.inner-toast {
  display: none;
  background-color: rgba(52, 59, 74, 1);
  color: #f1f1f1;
  border-radius: 2px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  padding: 1rem;
  margin-top: .625rem;
  word-spacing: 2px;
  letter-spacing: 1.5px;
}

.event-type {
  margin-bottom: 0.5rem;
}

[visible] {
  display: block;
}
`;
