import { LitElement, html } from 'lit-element';
import styles from './BbvaCoreDemoEventToaster-styles.js';

function _setEventsData(events) {
  return events.map(event => ({
    name: event,
    opened: false,
    payload: false,
    timeout: null,
    order: 0,
  }));
}

function _setEventPayload(e) {
  if (e.detail === null || e.detail === undefined) {
    return false;
  }

  let isStringifiable = true;

  try {
    JSON.stringify(e.detail);
  } catch (er) {
    isStringifiable = false;
  }
  /* eslint-disable no-console */
  if (typeof e.detail.nodeName === 'string') {
    console.info(`[bbva-core-demo-event-toaster]: Payload of ${e.type}`, e.detail);
    return `[HTMLElement] ${e.detail.nodeName} (more info in console)`;
  }

  if (!isStringifiable) {
    console.info(`[bbva-core-demo-event-toaster]: Payload of ${e.type}`, e.detail);
    return 'Payload couldn`t be stringifiable (payload info in console)';
  }
  /* eslint-enable no-console */

  if (JSON.stringify(e.detail) !== '{}') {
    return JSON.stringify(e.detail);
  }

  return false;
}

/*
`bbva-core-demo-event-toaster` adds an event listener for each event from the `events` array in the demo case iframe.
A toast is shown in demo helper each time one of the provided events is fired. The toast contains info on event type, flags (bubbles, composed, cancelable) and detail.

```html
<bbva-core-demo-event-toaster events='["event1", "event2", "event3"]'></bbva-core-demo-event-toaster>
```

* @customElement bbva-core-demo-dropdown-option
* @polymer
* @LitElement
*/
export class BbvaCoreDemoEventToaster extends LitElement {
  static get is() {
    return 'bbva-core-demo-event-toaster';
  }

  static get properties() {
    return {
      /**
       * Array of event names (strings)
       */
      events: {
        type: Array,
      },

      _eventsData: {
        type: Array,
        attribute: false,
      },

      /**
       * Duration for each event toast
       */
      duration: {
        type: Number,
      },

      /**
       * Toast opened status
       */
      opened: {
        type: Boolean,
        reflect: true,
      },

      /**
       * Aligns the toast relative to the top of the window instead of bottom
       */
      alignTop: {
        type: Boolean,
        attribute: 'align-top',
        reflect: true,
      },
    };
  }

  constructor() {
    super();
    this.duration = 4000;
    this.opened = false;
    this.alignTop = false;
    this._eventsData = [];
    this._setToast = this._setToast.bind(this);
  }

  get events() {
    return this._events;
  }

  set events(newValue) {
    const oldValue = this._events;
    if (newValue !== oldValue) {
      this._events = newValue;
      this._eventsData = _setEventsData(newValue);
      this._updateEventsListeners(newValue, oldValue);
      this.requestUpdate('events', oldValue);
    }
  }

  _updateEventsListeners(newEvents, oldEvents) {
    if (oldEvents) {
      oldEvents.forEach(event => window.removeEventListener(event, this._setToast, true));
    }
    newEvents.forEach(event => window.addEventListener(event, this._setToast, true));
  }

  firstUpdated(changedProps) {
    super.firstUpdated && super.firstUpdated(changedProps);
  }

  static get styles() {
    return styles;
  }

  render() {
    const items = this._eventsData.map(
      item => html`
        <div
          class="inner-toast"
          ?visible="${item.opened}"
          style="order: ${item.order}"
          @click="${() => this._closeToast(item)}"
        >
          <p class="event-type">Fired: <span class="highlight">${item.name}</span></p>
          <p class="event-flags">
            Bubbles: <span class="highlight">${item.bubbles}</span>; Composed:
            <span class="highlight">${item.composed}</span>; Cancelable:
            <span class="highlight">${item.cancelable}</span>
          </p>
          ${item.payload
            ? html`
                <p class="event-payload">
                  With payload: <span class="highlight">${item.payload}</span>
                </p>
              `
            : ''}
        </div>
      `,
    );
    return html`
      <div id="toast" class="toast">
        ${items}
      </div>
    `;
  }

  _setToast(ev) {
    const item = this._eventsData.find(event => event.name === ev.type);
    const index = this._eventsData.findIndex(event => event.name === ev.type);
    item.opened = true;
    item.payload = _setEventPayload(ev);
    item.bubbles = String(ev.bubbles);
    item.composed = String(ev.composed);
    item.cancelable = String(ev.cancelable);

    this._resetOpenedState(item);
    this._setOrder(index);
    this.opened = true;
    this.requestUpdate();
  }

  _closeToast(item) {
    const toast = item;
    toast.opened = false;
    clearTimeout(toast.timeout);
    if (!this._openedToasts) {
      this.opened = false;
    }
    this.requestUpdate();
  }

  _resetOpenedState(item) {
    const toast = item;
    toast.timeout = setTimeout(() => {
      this._closeToast(toast);
    }, this.duration);
  }

  _setOrder(index) {
    this._eventsData.forEach((event, eventIndex) => {
      if (event.order > 0) {
        this._eventsData[eventIndex].order = event.order - 1;
      }
    });
    this._eventsData[index].order = this._eventsData.length;
  }

  get _openedToasts() {
    return this._eventsData.find(item => item.opened === true);
  }
}
