import { css, } from 'lit-element';

export default css`
:host {
  display: block;
  box-sizing: border-box;
  padding: 0;
  margin-bottom: 1rem;
}

:host(:last-of-type) {
  margin-bottom: 0;
}

:host([hidden]), [hidden] {
  display: none!important;
}

*, *::before, *::after {
  box-sizing: inherit;
}

.content {
  display: flex;
  margin-bottom: .5rem;
}

.text {
  flex: auto;
  display: block;
  line-height: 1rem;
}

.check {
  flex: none;
  width: 1rem;
  height: 1rem;
  margin-left: 1rem;
  color: #fff;
}

svg {
  fill: currentColor;
}

.line {
  height: 0.5rem;
  display: flex;
  align-items: center;
  position: relative;
}

.line::after {
  display: block;
  content: '';
  height: 2px;
  background-color: #565C67;
  width: 100%;
}
:host([tabindex='0']) {
  background-color: transparent;
  color: #fff;
}
:host([tabindex='0']) .line::after {
  background: #fff;
}
:host([aria-selected='true']) {
  color: #fff;
}

:host([aria-selected='true'])::after {
  content: '';
  display: none;
}

`;
