import { html, svg } from 'lit-element';
import { BbvaCoreSelect } from '@bbva-web-components/bbva-core-select';
import styles from './BbvaCoreDemoDropdownMenu-styles.js';

const dropdownIcon = svg`
<svg viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
<path d="M6.555 7.168c-.46-.306-1.08-.182-1.387.277-.306.46-.182 1.08.277 1.387l3 2c.336.224.774.224 1.11 0l3-2c.46-.306.583-.927.277-1.387-.306-.46-.927-.583-1.387-.277L9 8.798l-2.445-1.63z" fill-rule="nonzero" />
</svg>
`;

function _checkMaxWidth(_maxWidth) {
  return _maxWidth ? `max-width:${_maxWidth}px;` : '';
}

function _checkMaxHeight(_maxHeight) {
  return _maxHeight ? `max-height:${_maxHeight}px;` : '';
}

/*
`bbva-core-demo-dropdown-menu` extends `bbva-core-select` to provide a custom select with custom styling and dropdown animation, as well as `bbva-core-demo-dropdown-option` tags for the select options. Otherwise, it works as `bbva-core-select` base element.

* @customElement bbva-core-demo-dropdown-menu
* @polymer
* @LitElement
*/
export class BbvaCoreDemoDropdownMenu extends BbvaCoreSelect {
  static get is() {
    return 'bbva-core-demo-dropdown-menu';
  }

  static get optionTag() {
    return 'bbva-core-demo-dropdown-option';
  }

  static get properties() {
    return {
      _layerDirection: {
        type: String,
        attribute: false,
      },

      _maxWidth: {
        type: Number,
        attribute: false,
      },

      _maxHeight: {
        type: Number,
        attribute: false,
      },

      _minWidth: {
        type: Number,
        attribute: false,
      },
    };
  }

  constructor() {
    super();
    this.safeMargin = 32;
    this.minHeight = 64;
    this._layerDirection = '';
  }

  get opened() {
    return this._opened;
  }

  set opened(value) {
    const oldValue = this._opened;
    if (value !== oldValue && !this.disabled) {
      this._opened = value;
      if (oldValue !== undefined) {
        this.setAttribute('animating', '');
      }
      if (value) {
        this._setSizes();
      }
      this.requestUpdate('opened', oldValue);
    }
  }

  firstUpdated(props) {
    super.firstUpdated && super.firstUpdated(props);
    this._layer = this.shadowRoot.querySelector('.options-layer');
    this._layerContent = this.shadowRoot.querySelector('.layer-content');
  }

  static get styles() {
    return [super.styles, styles];
  }

  get _controlContent() {
    return html`
      <span class="text">${this._selectedOption && this._selectedOption.text}</span>
      <span class="line">
        <span class="underline"></span>
        <span class="icon">
          ${dropdownIcon}
        </span>
      </span>
    `;
  }

  get _optionsLayer() {
    return html`
      <div
        class="options-layer ${this.opened ? 'opened' : ''} ${this._layerDirection}"
        style="${_checkMaxWidth(this._maxWidth)} ${_checkMaxHeight(this._maxHeight)}"
        @transitionend="${this._fixSize}"
      >
        <div class="layer-wrapper">
          <div class="layer-content" style="min-width:${this._minWidth}px;">
            ${this._optionsList}
          </div>
        </div>
      </div>
    `;
  }

  /**
   * Overrides bbva-core-select _onOptionFocusout to prevent reopening dropdown on button click
   */
  _onOptionFocusout() {
    setTimeout(() => {
      const newFocusedElement = this.shadowRoot.activeElement || this._rootNode.activeElement;
      if (this._options.indexOf(newFocusedElement) < 0 && newFocusedElement !== this._button) {
        this.opened = false;
      }
    }, 0);
  }

  /**
   * Sets direction classes on layer
   */
  _setSizes() {
    this._minWidth = this.offsetWidth;
    const elemRect = this.getBoundingClientRect();
    const dom = document.documentElement;
    /* istanbul ignore next */
    const viewW = Math.max(dom.clientWidth, window.innerWidth || 0);
    /* istanbul ignore next */
    const viewH = Math.max(dom.clientHeight, window.innerHeight || 0);
    const horizontal = this._checkHorizontal(elemRect, viewW);
    const vertical = this._checkVertical(elemRect, viewH);
    this._layerDirection = `${vertical} ${horizontal}`;
  }

  /**
   * Set max width of layer and returns horizontal direction
   */
  _checkHorizontal(opener, view) {
    const rightSpace = view - opener.left - this.safeMargin;
    const leftSpace = opener.right - this.safeMargin;
    let max;
    let result;
    if (rightSpace < this._minWidth && leftSpace > rightSpace) {
      max = leftSpace;
      result = 'right';
    } else {
      max = rightSpace;
      result = 'left';
    }
    this._maxWidth = max;
    return result;
  }

  /**
   * Set max height of layer and returns vertical direction
   */
  _checkVertical(opener, view) {
    const bottomSpace = view - opener.bottom - this.safeMargin;
    const topSpace = opener.top - this.safeMargin;
    let max;
    let result;
    if (bottomSpace < this.minHeight && topSpace > bottomSpace) {
      max = topSpace;
      result = 'bottom';
    } else {
      max = bottomSpace;
      result = 'top';
    }
    this._maxHeight = max;
    return result;
  }

  /**
   * Reset layer sizes
   */
  _fixSize() {
    this.removeAttribute('animating');
    this._layer.style.width = this.opened ? `${this._layerContent.offsetWidth + 1}px` : '100vw';
    if (!this.opened) {
      this._layerDirection = '';
      this._maxWidth = 0;
      this._maxHeight = 0;
    }
  }
}
